.movies-list {
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: #FAFAFA;
}

.movies-list__list {
  margin: 82px 0 0 0;
  padding: 0;
  padding: 0 0 30px 0;

  display: grid;
  justify-items: center;
  grid-template-columns: 1fr 1fr 1fr 1fr;

  row-gap: 32px;
  column-gap: 20px;

  list-style: none;
}

.movies-list__btn-more {
  margin: 50px 0 80px 0;
  padding: 0;

  width: 320px;
  height: 36px;

  color: #000;
  text-align: center;
  font-family: 'Inter', Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.25;

  border: 0;
  border-radius: 6px;

  outline: none;
  background-color: #F4F4F4;

  cursor: pointer;
}

.movies-list__btn-more:hover {
  background-color: #f5f5f5cc;
}

.movies-list__saveddevider {
  margin: 0;
  padding: 0;

  width: 100%;
  height: 140px;
}

.movies-list__not-found {
  margin: 0;
  padding: 40px 0;

  color: #000;
  font-family: 'Inter', Arial, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.11;
}

@media screen and (max-width: 1200px) {
  .movies-list__list {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (max-width: 965px) {

  .movies-list__list {
    margin: 80px 0 0;
    grid-template-columns: 1fr 1fr;
    row-gap: 36px;
    column-gap: 30px;
  }

  .movies-list__btn-more {
    margin: 50px 0 77px 0;
  }

}

@media screen and (max-width: 742px) {
  .movies-list__list {
    margin: 50px 0 0;
    grid-template-columns: 1fr;
    row-gap: 20px;
  }
}